.heading .headingtext::before {
  content: " ";
  width: 55px;
  position: absolute;
  top: -50%;
  left: 100%;
  margin-left: -75px;
  border: 3.5px solid #FFD92A;
  border-radius: 10px;
}
.heading .headingtext1::before{
  content: " ";
  width: 55px;
  position: absolute;
  top: -50%;
  left: 100%;
  margin-left: -90px;
  border: 3.5px solid #FFD92A;
  border-radius: 10px;
}

.mission-heading .mission-headingtext::before {
  margin-left: -110px;
}
.headingtext,.headingtext1{
  font-weight: bold;
  color: #B10F56 !important;
}

.outcomes-heading {
  color: #000;
  position: relative;
  /* display: inline-block; */
}

.outcomes-heading .outcomes-headingtext::before {
  content: " ";
  width: 120px;
  position: absolute;
  top: 110%;
  left: 45.5%;
  margin-top: 20px;
  /* margin-left: -210px; */
  border: 2px solid #172d8e;
}

@media (max-width : 991px) {
  .heading .headingtext::before {
    left: 115% !important;
  }
  .bg-outcome-container {
    padding: 50px 10px !important;
  }
  .col-bg1,.col-bg2{
    padding: 10px !important;
  }
  .footer-home {
    /* margin: 0; */
    padding: 60px 30px !important;
    /* padding-bottom: 40px; */
  }
  
}

.bg-outcome-container {
  padding: 50px;
  background-color: #E6F2FF;
}

.col-bg1 {
  color: #fff !important;
  background-color: #0C4DA2;
}

.col-bg1 ul li {
  padding: 10px;
}

.col-bg1 ul li img {
  margin-right: 10px;
}

.col-bg2 {
  color: #000 !important;
  background-color: #A8CBF6;
}

.col-bg2 ul li {
  padding: 10px;
}

.col-bg2 ul li img {
  margin-right: 10px;
}


.reveal {
  opacity: 0;
  transform: translateY(100px);
  transition: opacity 0.8s, transform 0.8s;
}

.reveal_visible {
  opacity: 1;
  transform: translateY(0);
}

.footer-home {
  background-color: #003a6a;
  margin: 0;
  padding: 100px;
  /* padding-bottom: 100px; */
}
.text-primary{
  text-decoration: underline;
}
.nav-item{
  padding-left: 10px;
}

@media(max-width:768px) {
  .site-footer {
    padding: 100px 0 !important; 
  }
}

.site-footer p {
  color: #737373
}

.site-footer h2,
.site-footer h3,
.site-footer h4,
.site-footer h5 {
  color: #fff
}

.site-footer a {
  color: #999
}

.site-footer a:hover {
  color: white
}

.site-footer ul li {
  margin-bottom: 10px
}

.site-footer .footer-heading {
  font-size: 20px;
  color: #fff
}